.container {
    position: absolute;
    width: calc(100% - 16px);
    background-color: #c50000;
    top: 64px;
    margin-left: 8px;
    margin-right: 8px;
    padding: 8px;
    border-radius: 8px;
    white-space: pre;
    opacity: 0;
    transition: opacity 0.5s ease;
    text-overflow: clip;
}